}
table.work-table .errorPlaceholder::-webkit-input-placeholder,
  table.work-table .error::-webkit-input-placeholder {
    /* 크롬 4–56 */
    color: #ff1919 !important;
}
table.work-table .errorPlaceholder:-moz-placeholder,
  table.work-table .error:-moz-placeholder {
    /* 파이어폭스 4–18 */
    color: #ff1919 !important;
    opacity: 1 !important;
}
table.work-table .errorPlaceholder:-ms-input-placeholder,
  table.work-table .error:-ms-input-placeholder {
    /* 인터넷 익스플로러 10+ */
    color: #ff1919 !important;
