table.work-table .errorPlaceholder::-ms-input-placeholder,
  table.work-table .error::-ms-input-placeholder {
    /* 파이어폭스 51+, 크롬 57+ */
    color: #ff1919 !important;
    opacity: 1 !important;
}
table.work-table .errorPlaceholder::placeholder,
  table.work-table .error::placeholder {
    /* 파이어폭스 51+, 크롬 57+ */
    color: #ff1919 !important;
    opacity: 1 !important;
}
table.work-table .errorMessage {
    color: #ff1919;
}
